import useAxios, { configure } from 'axios-hooks';
import axios from '../Axios';
import { useState, useEffect } from 'react';
import Table from '../components/Table';
import Spinner from '../components/Spinner';
import { NavLink } from 'react-router-dom';
import dayjs from 'dayjs';
import styled from 'styled-components';

const LinkContainer = styled.div`
  position: sticky;
  top: 0;
  background-color: #fff;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  padding: 10px 0;
`;
const TopLink = styled(NavLink)`
  margin-right: 15px;
  display: inline-block;
  font-size: 16px;
  padding: 7px 10px 5px 10px;
  border: 1px solid #ccc;
  background-color: #fff;
  color: #000;
  text-decoration: none;

  &:hover {
    background-color: #06f2;
  }
`;

const ProfessorList = () => {
  //axios 데이터  불러오기
  const [{ data, loading, error }] = useAxios('/professor', {
    useCache: false, //자체 캐시기능 방지
  });
  configure({ axios });

  const [{ loading: loading2 }, sendDelete] = useAxios(
    '/professor',
    {
      method: 'DELETE',
    },
    {
      useCache: false,
      manual: true,
    }
  );
  const [professor, setProfessor] = useState([]);

  const onDelete = (e) => {
    e.preventDefault();

    const current = e.target;
    const id = parseInt(current.dataset.id);
    const name = current.dataset.name;

    if (window.confirm(`정말 ${name} 교수의 정보를 삭제하시겠습니까?`)) {
      (async () => {
        let json = null;
        try {
          const res = await sendDelete({
            url: `/professor/${id}`,
            method: 'DELETE',
          });
          json = res.data;
        } catch (e) {
          console.error(e);
        }

        if (json != null) {
          setProfessor((professor) => professor.filter((v) => v.id !== id));
        }
      })();
    }
  };

  useEffect(() => {
    setProfessor(data);
    console.log(data);
  }, [data]);

  return (
    <>
      <Spinner visible={loading || loading2} />
      <LinkContainer>
        <TopLink to="/add">교수 등록하기</TopLink>
      </LinkContainer>
      {error ? (
        <div>
          <h1>{error.code} Error</h1>
          <p>{error.message}</p>
        </div>
      ) : (
        <Table>
          <thead>
            <tr>
              <th>No.</th>
              <th>이름</th>
              <th>아이디</th>
              <th>직급</th>
              <th>급여</th>
              <th>입사일</th>
              <th>보직수당</th>
              <th>소속학과번호</th>
              <th>수정</th>
              <th>삭제</th>
            </tr>
          </thead>
          <tbody>
            {professor &&
              professor.map(({ id, name, userid, position, sal, hiredate, comm, deptno }) => {
                return (
                  <tr key={id}>
                    <td>{id}</td>
                    <td>{name}</td>
                    <td>{userid}</td>
                    <td>{position}</td>
                    <td>{sal}만원</td>
                    <td>{dayjs(hiredate).format('YY/MM/DD')}</td>
                    <td>{!!comm && `${comm}만원`}</td>
                    <td>{deptno}</td>
                    <td>
                      <NavLink to={`edit/${id}`}>수정하기</NavLink>
                    </td>
                    <td>
                      <a href="#!" data-id={id} data-name={name} onClick={onDelete}>
                        삭제하기
                      </a>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </Table>
      )}
    </>
  );
};

export default ProfessorList;
