import useAxios, { configure } from 'axios-hooks';
import styled from 'styled-components';
import Spinner from '../components/Spinner';
import Table from '../components/Table';
import regexHelper from '../components/libs/RegexHelper';

import axios from '../Axios';
import { useNavigate, useParams } from 'react-router-dom';
import dayjs from 'dayjs';
import { useCallback } from 'react';

const TableEx = styled(Table)`
  margin-top: 50px;
  margin-bottom: 15px;
  .inputWrapper {
    padding: 0;
    position: relative;
    text-align: left;
    .field {
      box-sizing: border-box;
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 0;
      padding: 0 10px;
      outline: none;
      font-size: 14px;
    }
    label {
      margin-left: 7px;
      margin-right: 10px;
      input {
        margin-right: 10px;
      }
    }
  }
`;

const ProfessorEdit = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  configure({ axios });
  const [{ data, loading: loading1, error }] = useAxios('/department');
  const [{ loading: loading2 }, refetch] = useAxios(
    {
      url: `/professor/${id}`,
      method: 'PUT',
    },
    { manual: true }
  );
  const [{ data: professor, loading: loading3, error2 }] = useAxios(`/professor/${id}`);

  const onSubmit = useCallback(
    (e) => {
      e.preventDefault();
      const current = e.target;

      try {
        regexHelper.value(current.name, '이름을 입력하세요');
        regexHelper.kor(current.name, '한글로만 입력하세요');
        regexHelper.minLength(current.name, 2, '이름을 2자 이상 입력하세요');
        regexHelper.maxLength(current.name, 10, '이름은 10자 이하로 입력하세요');
        regexHelper.value(current.userid, '아이디는 필수입니다');
        regexHelper.engNum(current.userid, '아이디는 영문과 숫자로 입력하세요');
        regexHelper.minLength(current.userid, 2, '아이디를 2자 이상 입력하세요');
        regexHelper.maxLength(current.userid, 20, '아이디를 20자 이하로 입력하세요');
        regexHelper.check(current.position, '직급은 입력하세요');
        regexHelper.value(current.sal, '급여를 입력하세요');
        regexHelper.num(current.sal, '급여를 숫자로 입력하세요');
        regexHelper.value(current.hiredate, '입사일을 입력하세요');
        regexHelper.value(current.comm, '보직수당을 입력하세요');
        regexHelper.num(current.comm, '보직수당을 숫자로 입력하세요');
        regexHelper.value(current.deptno, '소속학과를 입력하세요');
      } catch (e) {
        window.alert(e.message);
        e.field.focus();
        return;
      }
      let json = null;

      const name = current.name.value;
      const userid = current.userid.value;
      const position = current.position.value;
      const sal = current.sal.value;
      const hiredate = dayjs(current.hiredate.value).toISOString();
      const comm = parseInt(current.comm.value);
      const deptno = parseInt(current.deptno.value);

      (async () => {
        try {
          const res = await refetch({
            data: {
              name,
              userid,
              position,
              sal,
              hiredate,
              comm,
              deptno,
            },
          });
          json = res.data;
        } catch (e) {
          console.error(e);
        }
        if (json !== null) {
          window.alert('저장되었습니다');
          navigate('/');
        }
      })();
    },
    [navigate, refetch]
  );

  return (
    <>
      <Spinner visible={loading1 || loading2 || loading3} />
      {error || error2 ? (
        <div>
          <h1>{error.code} Error</h1>
          <p>{error.message}</p>
        </div>
      ) : (
        !loading3 && (
          <form onSubmit={onSubmit}>
            <TableEx>
              <colgroup>
                <col width="120" />
                <col />
              </colgroup>
              <tbody>
                <tr>
                  <th>이름</th>
                  <td className="inputWrapper">
                    <input className="field" type="text" name="name" defaultValue={professor.name} />
                  </td>
                </tr>
                <tr>
                  <th>아이디</th>
                  <td className="inputWrapper">
                    <input className="field" type="text" name="userid" defaultValue={professor.userid} />
                  </td>
                </tr>
                <tr>
                  <th>직급</th>
                  <td className="inputWrapper">
                    <label>
                      <input type="radio" name="position" value="교수" defaultChecked={'교수' === professor.position} />
                      교수
                    </label>
                    <label>
                      <input type="radio" name="position" value="부교수" defaultChecked={'부교수' === professor.position} />
                      부교수
                    </label>
                    <label>
                      <input type="radio" name="position" value="조교수" defaultChecked={'조교수' === professor.position} />
                      조교수
                    </label>
                    <label>
                      <input type="radio" name="position" value="전임강사" defaultChecked={'전임강사' === professor.position} />
                      전임강사
                    </label>
                  </td>
                </tr>
                <tr>
                  <th>급여</th>
                  <td className="inputWrapper">
                    <input className="field" type="number" name="sal" placeholder="숫자만 입력" defaultValue={professor.sal} />
                  </td>
                </tr>
                <tr>
                  <th>입사일</th>
                  <td className="inputWrapper">
                    <input className="field" type="date" name="hiredate" defaultValue={dayjs(professor.hiredate).format('YYYY-MM-DD')} />
                  </td>
                </tr>
                <tr>
                  <th>보직수당</th>
                  <td className="inputWrapper">
                    <input className="field" type="number" name="comm" placeholder="숫자만 입력" defaultValue={professor.comm} />
                  </td>
                </tr>
                <tr>
                  <th>소속학과</th>
                  <td className="inputWrapper">
                    <select name="deptno" className="field" defaultValue={professor.deptno}>
                      <option value=""> ---학과--- </option>
                      {data &&
                        data.map(({ dname, id }) => {
                          return (
                            <option key={id} value={id}>
                              {dname}
                            </option>
                          );
                        })}
                    </select>
                  </td>
                </tr>
              </tbody>
            </TableEx>
            <div style={{ textAlign: 'center' }}>
              <button type="submit">저장하기</button>
            </div>
          </form>
        )
      )}
    </>
  );
};
export default ProfessorEdit;
